@import url('https://fonts.googleapis.com/css2?family=REM:wght@400&display=swap');

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'REM', sans-serif;
  font-size: 1rem;
  letter-spacing: 0.7px;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.3rem 0.1rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: #f8df00;
  color: #213555;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  padding: 0.3rem 0.1rem;
  font: inherit;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 4px;
    font-size: 1.1rem;
  }

  .nav-links li {
    margin: 0 0.4rem;
  }

  .nav-links a {
    color: white;
    background: #213555;
    text-decoration: none;
    border-radius: 8px;
  }

  .nav-links button {
    border: 1px solid white;
    color: white;
    background: #213555;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background:#eae3d2;
    color: #292929;
  }
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    background: #eae3d2;
    color: #292929;
  }
}